body * {
  transition: all 0.1s;
}
.c-line-clamp2,
.c-line-clamp3,
.c-line-clamp4,
.c-line-clamp5 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 0.05rem;
  white-space: normal;
}
.c-line-clamp2 {
  -webkit-line-clamp: 2;
}
.c-line-clamp3 {
  -webkit-line-clamp: 2;
}
.c-line-clamp4 {
  -webkit-line-clamp: 2;
}
.c-line-clamp5 {
  -webkit-line-clamp: 2;
}
.c-line-clamp1 {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
